input[type=text],
input[type=submit],
input[type=email],
textarea {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent; }

fieldset {
  border: none;
  margin: 0; }

legend {
  display: none; }

.c-form h3 {
  margin: 0 0 12px; }

.c-form input[type=radio] {
  display: none;
  margin: 0; }

.c-form input[type=radio]:checked + .c-form__labelRadio:after {
  left: 6px;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  background: #000;
  border-radius: 8px;
  content: "";
  position: absolute;
  top: 50%;
  box-sizing: border-box;
  display: block; }

.c-form__labelRadio {
  position: relative;
  display: inline-block;
  margin-right: 28px;
  line-height: 1.5px;
  cursor: pointer;
  padding: 0 0 0 30px; }
  .c-form__labelRadio:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    box-sizing: border-box;
    display: block;
    width: 22px;
    height: 22px;
    margin-top: -11px;
    background-color: white;
    border-radius: 50%; }

.c-form input[type=checkbox] {
  display: none;
  margin: 0; }

.c-form input[type=checkbox]:checked + .c-form__labelCheck:after {
  left: 3px;
  width: 16px;
  height: 8px;
  margin-top: -6px;
  border-left: 3px solid #000;
  border-bottom: 3px solid #000;
  transform: rotate(-45deg);
  content: "";
  position: absolute;
  top: 50%;
  box-sizing: border-box;
  display: block; }

.c-form__labelCheck {
  position: relative;
  display: inline-block;
  margin-right: 28px;
  line-height: 1.5px;
  cursor: pointer;
  padding: 0 0 0 30px; }
  .c-form__labelCheck:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    box-sizing: border-box;
    display: block;
    width: 22px;
    height: 22px;
    margin-top: -11px;
    background-color: white; }

.c-form__lead {
  font-size: 18px; }
  .c-form__leadText {
    margin: 0 0 40px; }

.c-form__errorWrap {
  margin: 0 0 40px; }
  .c-form__errorWrap__title {
    color: #EF5A24;
    font-weight: bold;
    font-size: 16px;
    margin: 0 0 12px; }
  .c-form__errorWrap__list {
    border: 2px solid #EF5A24;
    padding: 20px 20px 12px;
    border-radius: 4px;
    list-style: none;
    margin: 0; }
  .c-form__errorWrap__item {
    color: #EF5A24;
    font-size: 16px;
    margin: 0 0 12px; }

.c-form__group {
  margin: 0 0 32px; }

.c-form__select {
  overflow: hidden;
  border: 1px solid #333;
  position: relative;
  width: 180px;
  display: inline-block; }
  .c-form__select:before {
    content: "";
    position: absolute;
    right: 10px;
    top: 50%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 6px 0 6px;
    border-color: #333 transparent transparent;
    margin: -3px 0 0; }
  .c-form__select select {
    width: 100%;
    padding: 0 10px;
    border: 0;
    height: 40px;
    appearance: none;
    background: #fff;
    display: inline-block;
    width: 210px; }

.c-form__inputText, .c-form__textArea {
  width: 100%;
  border: 1px solid #666;
  padding: 8px 10px;
  box-sizing: border-box;
  background: #fff; }

.c-form__must {
  font-size: 12px;
  color: #fff;
  background: #999;
  line-height: 18px;
  height: 18px;
  padding: 0 10px;
  display: inline-block;
  border-radius: 9px;
  margin: 0 0 0 8px;
  vertical-align: middle; }

.c-form__column {
  display: table;
  width: 80%; }
  .c-form__columnTitle {
    display: table-cell;
    vertical-align: top;
    padding: 0 18px 0 0;
    width: 200px; }
  .c-form__columnBody {
    display: table-cell;
    vertical-align: top; }

.c-form__privacy {
  margin: 0 0 40px;
  padding: 1em;
  border: 1px solid #333;
  background: #fff;
  height: 100px;
  overflow: hidden;
  overflow: auto; }
  .c-form__privacyInner {
    font-size: 12px;
    line-height: 2; }

.c-form__btnWrap {
  text-align: center;
  margin: 0 0 40px; }

body {
  color: #333;
  font-size: 16px;
  font-family: "游ゴシック体", "Yu Gothic", YuGothic, "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
  line-height: 1.6; }

html > body {
  min-width: 980px; }

a {
  outline: none;
  -webkit-touch-callout: none; }

a:hover {
  text-decoration: none; }

a:focus,
*:focus {
  outline: none; }

img {
  vertical-align: top;
  border: none;
  border-style: none; }

a {
  opacity: 1;
  -moz-transition: opacity .2s ease-in-out;
  -webkit-transition: opacity .2s ease-in-out;
  transition: opacity .2s ease-in-out; }
  a:hover {
    opacity: 0.7; }
    a:hover img {
      background-color: rgba(255, 255, 255, 0.01); }

br.SPbr {
  display: none; }
  @media screen and (max-width: 767px) {
    br.SPbr {
      display: inline; } }

br.PCbr {
  display: inline; }
  @media screen and (max-width: 767px) {
    br.PCbr {
      display: none; } }

.SPview {
  display: none; }
  @media screen and (max-width: 767px) {
    .SPview {
      display: block; } }

.PCview {
  display: block; }
  @media screen and (max-width: 767px) {
    .PCview {
      display: none; } }

